<template>
  <div class="histogram">
    <div class="wrap" :style="{height:height+'px','line-height':height+'px'}">
      <span class="name" :style="{width: nameWidth+'px','font-size':fontSize+'px',opacity: show?1:0}">{{name}}</span>
      <div class="progress" :style="[progressClass,{width:width+'px'}]"></div>
      <span class="percentage" :style="[percentageClass,{'font-size':fontSize+'px'}]">{{percentage}}%</span>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      name: {
        type: String,
        default: 'html'
      },
      nameWidth: {
        type: Number,
        default: 50
      },
      percentage: {
        type: Number,
        default: 100
      },
      width: {
        type: Number,
        default: 350
      },
      height: {
        type: Number,
        default: 22
      },
      fontSize: {
        type: Number,
        default: 16
      },
      bgColor: {
        type: String,
        default: '#f78'
      },
      show: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        progressClass: {
          opacity: 0,
          background: this.bgColor
        },
        percentageClass: {
          opacity: 0
        }
      }
    },
    watch: {
      show () {
        if (!this.show) {
          this.percentageClass.opacity = 0;
          this.percentageClass.transition = '1s';
          this.progressClass.opacity = 0;
        } else {
          this.percentageClass.opacity = 1;
          this.percentageClass.transition = '1s 1.3s';
          this.progressClass.opacity = 1;
        }
      }
    }
  }
</script>

<style lang="scss" scope>
  %float {
    float: left;
    vertical-align: middle;
  }
  .name {
    text-align: right;
    transition: 2s;
    @extend %float;
  }
  .progress {
    height: 100%;;
    margin: 0 12px;
    border-radius: 4px;
    transition: 1.5s;
    @extend %float;
  }
  .percentage {
    @extend %float;
  }
</style>


