<template>
  <div class="section" @click="hideAlert">
    <heading :content="'联系我'" :show="show"></heading>
    <transition name="left-in">
      <div class="motto" v-show="show">
        <ul>
          <li class="motto-item" v-for="(item,index) in mottoList" :key="index">
            {{item}}
          </li>
        </ul>
    </div>
    </transition>
    <transition name="right-in">
      <div class="text" v-show="show">
        <ul>
          <li class="text-item" v-for="(text,index) in textList" :key="index">
            {{text}}
          </li>
        </ul>
    </div>
    </transition>
    <div class="contact">
      <transition-group name="bounce" tag="ul">
        <li class="contact-item" v-for="(contactItem, index) in contactList" :key="index" v-show="show">
          <img class="contact-icon" :src="contactItem" @click.stop="showAlert(index)" ref="hookIcon">
          <transition name="fade">
            <div class="alert" v-show="alertShow===index" ref="hookAlert">
            <img class="contact-pic" v-if="contactContent[index].img" :src="contactContent[index].img">
            <p>{{contactContent[index].text}}</p>
            <div class="triangle" ref="hookTriangle"></div>
          </div>
          </transition>
        </li>
      </transition-group>
    </div>
  </div>
</template>

<script>
  import heading from './heading.vue'
  export default {
    components: {
      heading
    },
    props: ['show'],
    data () {
      return {
        mottoList: ['兴趣','梦想','务实','勤奋'],
        textList: ['学习是一个充实愉悦的过程',
        '喜欢尝试，期待新鲜事物',
        '前端即兴趣，兴趣即未来',
        '行路有良友，便是捷径',
        '期待有机会与您共事！'],
        contactList: ['../../static/img/qq.png',
        '../../static/img/wechat.png',
        '../../static/img/call.png',
        '../../static/img/email.png'],
        contactContent: [{text:'516415408', img: '../../static/img/qqnum.png'},
          {text: '516415408', img: '../../static/img/wechatnum.png'},
          {text: '18650622719'},
          {text: '516415408@qq.com'}],
          alertShow: -1
      }
    },
    watch: {
      show () {
        this.alertShow = -1;
      }
    },
    methods: {
      alertLeft () {
        this.$nextTick(() => {
          let hookIcon = this.$refs.hookIcon[this.alertShow];
          let hookAlert = this.$refs.hookAlert[this.alertShow];
          let hookTriangle = this.$refs.hookTriangle[this.alertShow];
          hookAlert.style.left = -(hookAlert.offsetWidth - hookIcon.offsetWidth)/2 + 'px';
          hookTriangle.style.left = (hookAlert.offsetWidth - parseInt(getComputedStyle(hookTriangle,null).borderTopWidth))/2 + 'px';
        });
      },
      showAlert (index) {
        this.alertShow = index;
        this.alertLeft();
      },
      hideAlert () {
        this.alertShow = -1;
      }
    }
  }
</script>

<style lang="scss" scope>
  %center {
    width: 500px;
    margin: 0 auto;
    margin-top: 40px;
  }
  @mixin slide-in($translate) {
    transition: 1s;
    transform: $translate;
  }
  @mixin slide-out($translate) {
    transform: $translate;
    opacity: 0;
  }
  .motto {
    @extend %center;
    ul {
      display: flex;
      justify-content: space-between;
      .motto-item {
        flex: 1;
        text-align: center;
        font-size: 24px;
        $colorList: salmon, green, orange, blue;
        @for $i from 1 to 5 {
          &:nth-of-type(#{$i}) {
            color: nth($colorList, $i);
          }
        }
      }
    }
  }
  .left-in-enter-active, .left-in-move-active {
    @include slide-in(translateX(0));
  }
  .left-in-enter, .left-in-leave-to {
    @include slide-out(translateX(-400px));
  }
  .text {
    @extend %center;
    .text-item {
      text-align: center;
      font-size: 20px;
      margin: 15px 0;
    }
  }
  .right-in-enter-active, .right-in-leave-active {
    @include slide-in(translateX(0));
  }
  .right-in-enter, .right-in-leave-to {
    @include slide-out(translateX(400px));
  }
  .contact {
    margin: 50px auto 0;
    width: 300px;
    ul {
      display: flex;
      justify-content: space-between;
      .contact-item {
        width: 40px;
        height: 40px;
        text-align: center;
        position: relative;
        @for $i from 1 to 5 {
          &:nth-of-type(#{$i}) {
            transition: 0.7s #{$i/2.5}s;
          }
        }
        .contact-icon {
          width: 40px;
          height: 40px;
        }
        .alert {
          position: absolute;
          bottom: 55px;
          background: skyblue;
          border: 2px solid skyblue;
          border-radius: 4px;
          .contact-pic {
            width: 100%;
            height: 100%;
          }
          p {
            font-size: 14px;
            margin: 5px;
          }
          .triangle {
            position: absolute;
            bottom:　-14px;
            content: '';
            width: 0;
            height: 0;
            border-right: 7px solid transparent;
            border-left: 7px solid transparent;
            border-top: 14px solid skyblue;
          }
        }
        .fade-enter-active, .fade-leave-active {
          transition: 1s;
        }
        .fade-enter, .fade-leave-to {
          opacity: 0;
        }
      }
      .bounce-enter-active, .bounce-leave-ative {
        @include slide-in(translateY(0));
      }
      .bounce-enter, .bounce-leave-to {
        @include slide-out(translateY(300px));
      }
      .bounce-leave-active {
        transition: 500ms 0s !important;
      }
    }
  }
  @media screen and (max-width: 320px) {
    %center {
      width: 200px;
      margin-top: 20px;
    }
    .motto {
      ul {
        .motto-item {
          font-size: 18px;
        }
      }
    }
    .text {
      .text-item {
        font-size: 14px;
        margin: 10px 0;
      }
    }
    .contact {
      width: 160px;
      margin: 20px auto 0;
      ul {
        .contact-item {
          width: 30px;
          height: 30px;
          .contact-icon {
            width: 30px;
            height: 30px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 321px) and (max-width: 460px) {
    %center {
      width: 250px;
      margin-top: 22px;
    }
    .motto {
      ul {
        .motto-item {
          font-size: 19px;
        }
      }
    }
    .text {
      .text-item {
        font-size: 16px;
        margin: 11px 0;
      }
    }
    .contact {
      width: 200px;
      margin: 22px auto 0;
      ul {
        .contact-item {
          width: 32px;
          height: 32px;
          .contact-icon {
            width: 32px;
            height: 32px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    %center {
      width: 300px;
      margin-top: 27px;
    }
    .motto {
      ul {
        .motto-item {
          font-size: 20px;
        }
      }
    }
    .text {
      .text-item {
        font-size: 17px;
        margin: 12px 0;
      }
    }
    .contact {
      width: 250px;
      margin: 27px auto 0;
      ul {
        .contact-item {
          width: 33px;
          height: 33px;
          .contact-icon {
            width: 33px;
            height: 33px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    %center {
      width: 400px;
      margin-top: 30px;
    }
    .motto {
      ul {
        .motto-item {
          font-size: 22px;
        }
      }
    }
    .text {
      .text-item {
        font-size: 18px;
        margin: 15px 0;
      }
    }
    .contact {
      width: 290px;
      margin: 30px auto 0;
      ul {
        .contact-item {
          width: 35px;
          height: 35px;
          .contact-icon {
            width: 35px;
            height: 35px;
          }
        }
      }
    }
  }
</style>


