<template>
  <div class="title">
    <h1 class="text" :style="{opacity:show?1:0}">{{content}}</h1>
    <div class="line" :style="{width:!show?0:''}"></div>
  </div>
</template>

<script>
  export default {
    props: ['content','show']
  }
</script>

<style lang="scss" scope>
  .title {
    .text {
      margin-top: 130px;
      text-align: center;
      width: 100%;
      transition: 1s;
      font: {
        size: 27px;
        weight: 550;
      }
    }
    .line {
      height: 2px;
      width: 110px;
      margin: 7px auto 0;
      background: #f78;
      transition: 2s 300ms;
    }
  }
  @media screen and (max-width: 460px) {
    .title .text {
      font-size: 18px;
      margin-top: 70px;
    }
    .title .line {
      height: 1px;
      width: 70px;
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    .title .text {
      font-size: 21px;
      margin-top: 80px;
    }
    .title .line {
      width: 80px;
    }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    .title .text {
      font-size: 23px;
      margin-top: 100px;
    }
    .title .line {
      width: 90px;
    }
  }
  @media screen and (min-width: 901px) and (max-width: 1200px) {
    .title .text {
      font-size: 25px;
      margin-top: 110px;
    }
    .title .line {
      width: 100px;
    }
  }
</style>


