<template>
  <div class="section">
    <heading :content="'技能'" :show="show"></heading>
    <div class="content">
      <div class="histogram-wrap">
        <histogram :show="show" :name="item.name" :name-width="afterWidth/4" :percentage="item.percent" 
        :width="width*item.percent/100" :height="height" :bgColor="item.color" :font-size="font"
        v-for="(item, index) in dataList" :key="index"></histogram>
      </div>
      <div class="skill animated" :class="show? 'flipInX': 'fadeOut'">
        <ul class="text">
          <li>
            熟悉VueJs处理PC端、移动端的数据渲染，模块组件化，熟练的指令编写
          </li>
          <li>
            熟悉JavaScript、Ajax/jsonp、DOM等前端技术
          </li>
          <li>
            熟悉使用jQuery类库，实现网页特效和用户交互体验
          </li>
          <li>
            掌握html+css，切图和页面布局,熟悉css3新增属性
          </li>
          <li>
            能使用scss进行css样式的编写，了解bootstrap基本使用
          </li>
          <li>
            有强烈的求知欲，热爱前端技术，乐观，有较强的学习能力；
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import histogram from './histogram.vue'
  import heading from './heading.vue'
  export default {
    props: ['show'],
    data () {
      return {
        width: 0,
        afterWidth: 300,
        height: 22,
        font: 16,
        dataList: []
      }
    },
    components: {
      histogram,
      heading
    },
    watch: {
      show () {
        this.width = this.show? this.afterWidth: 0;
      },
      afterWidth () {
        this.width = this.show? this.afterWidth: 0;
      }
    },
    methods: {
      getData () {
        this.$axios.get('../../static/data.json').then((res) => {
          this.dataList = res.data.histogram;
        }, (err) => {
          console.log(err);
        });
      },
      mediaQuery () {
        if (window.matchMedia('(max-width: 460px)').matches) {
          this.afterWidth = 210;
          this.font = 12;
          this.height = 16;
        } else if (window.matchMedia('(min-width: 461px) and (max-width: 640px)').matches) {
          this.afterWidth = 300;
          this.height = 18;
          this.font = 13;
        } else if (window.matchMedia('(min-width: 641px) and (max-width: 900px)').matches) {
          this.afterWidth = 400;
          this.height = 20;
          this.font = 15;
        } else if (window.matchMedia('(min-width: 901px) and (max-width: 1200px)').matches) {
          this.afterWidth = 250;
        }
      },
      resize () {
        window.onresize = () => {
          this.mediaQuery();
        }
      }
    },
    mounted () {
      this.getData();
      this.mediaQuery();
      this.resize();
    }
  }
</script>

<style lang="scss" scope>
  .content {
    position: relative;
    top: 80px;
    width: 1000px;
    height:100%;
    margin: 0 auto;
    %position {
      position: absolute;
      top: 0;
    }
    .histogram-wrap {
      left: 0;
      width: 100%;
      @extend %position;
      .histogram {
        margin-bottom: 15px;
      }
    }
    .skill {
      right: 0;
      animation-duration: 2s;
      animation-delay: 0.5s;
      @extend %position;
      .text {
        li {
          margin: 25px 0;
        }
      }
    }
  }
  @media screen and (max-width: 320px) {
    .content {
      top: 15px;
      width: 100%;
      %position {
        position: static;
      }
      .histogram-wrap {
        .histogram {
          margin-bottom: 7px;
          .wrap {
            width: 91%;
            margin: 0 auto;
          }
        }
      }
      .skill {
        padding: 1px 20px 0;
        .text {
          li {
            margin: 10px 0;
            font-size: 12px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 321px) and (max-width: 460px) {
    .content {
      top: 30px;
      width: 100%;
      %position {
        position: static;
      }
      .histogram-wrap {
        .histogram {
          margin-bottom: 10px;
          .wrap {
            width: 300px;
            margin: 0 auto;
          }
        }
      }
      .skill {
        width: 300px;
        margin: 35px auto 0;
        .text {
          li {
            margin: 15px 0;
            font-size: 13px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    .content {
      top: 30px;
      width: 100%;
      %position {
        position: static;
      }
      .histogram-wrap {
        .histogram {
          margin-bottom: 10px;
          .wrap {
            width: 400px;
            margin: 0 auto;
          }
        }
      }
      .skill {
        width: 400px;
        margin: 35px auto 0;
        .text {
          li {
            margin: 15px 0;
            font-size: 15px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    .content {
      top: 40px;
      width: 100%;
      %position {
        position: static;
      }
      .histogram-wrap {
        .histogram {
          margin-bottom: 10px;
          .wrap {
            width: 550px;
            margin: 0 auto;
          }
        }
      }
      .skill {
        width: 500px;
        margin: 35px auto 0;
        .text {
          li {
            margin: 15px 0;
            font-size: 15px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 901px) and (max-width: 1200px) {
    .content {
      top: 50px;
      width: 850px;
      .skill {
        width: 400px;
        .text {
          li {
            margin: 20px 0;
          }
        }
      }
    }
  }
</style>


