<template>
  <div class="section">
    <heading :content="'作品'" :show="show"></heading>
    <div class="tips" :style="{opacity: show?1:0}">
      <p>加载慢请稍等</p>
    </div>
    <div class="swiper-container" :style="{opacity:show?1:0}">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in demoList" :key="index">
          <a :href="urlList[index]" target="_blank"><img :src="item"></a>
          <div class="script">
            <span>{{demoScript[index]}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
  import heading from './heading.vue'
  import 'swiper/dist/css/swiper.min.css'
  import 'swiper/dist/js/swiper.min.js'
  export default {
    props: ['show'],
    components: {
      heading
    },
    data () {
      return {
        demoList: ['../../static/img/news.png',
        '../../static/img/elm.png',
        '../../static/img/sale.png',
        '../../static/img/todo.png',
        '../../static/img/dianshang.png'],
        demoScript: ['vue制作的简易手机新闻页面',
        'vue制作的仿饿了么手机页面，样式使用scss编写',
        'vue制作的PC页面',
        '使用jQuery制作的类todo页面，使用localstorage储存',
        '简易静态PC页面，使用原生JS和jQuery写'
        ],
        urlList: ['https://516415408.github.io/news-demo',
          'https://516415408.github.io/elm',
          'https://516415408.github.io/sale-demo',
          'https://516415408.github.io/todo-demo/demo.html',
          'https://516415408.github.io/dianshang/'
        ]
      }
    },
    mounted () {
      this.swiper = new Swiper('.swiper-container', {
        effect : 'coverflow',
        slidesPerView: 3,
        grabCursor: true,
        loop: true,
        coverflow: {
            rotate: 30,
            stretch: 10,
            depth: 60,
            modifier: 2,
            slideShadows : true
        }
      });
    }
  }
</script>

<style lang="scss" scope>
  .swiper-container {
    width: 800px;
    margin: 50px auto 0;
    transition: 5s;
  }
  .swiper-slide {
    height: 250px;
    img {
      width: 100%;
      height: 100%;
      max-height: 250px;
    }
    .script {
      height: 50px;
      line-height: 50px;
      background: rgba(0, 0, 0, 0.4);
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      text-align: center;
      span {
        vertical-align: middle;
        font-size: 14px;
      }
    }
  }
  .tips {
    text-align: center;
    font-size: 16px;
    margin-top: 80px;
    transition: 1s;
  }
  @media screen and (max-width: 320px) {
    .swiper-container {
      width: 270px;
      margin: 20px auto 0;
      .swiper-slide {
        height: 100px;
      }
      .script {
        height: 35px;
        line-height: 10px;
        span {
          font-size: 10px;
        }
      }
    }
    .tips {
        font-size: 14px;
        margin-top: 30px;
      }
  }
  @media screen and (min-width: 321px) and (max-width: 460px) {
    .swiper-container {
      width: 330px;
      margin: 30px auto 0;
      .swiper-slide {
        height: 150px;
      }
      .script {
        height: 45px;
        line-height: 12px;
        span {
          font-size: 11px;
        }
      }
    }
    .tips {
        font-size: 15px;
        margin-top: 40px;
      }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    .swiper-container {
      width: 460px;
      margin: 30px auto 0;
      .swiper-slide {
        height: 150px;
      }
      .script {
        height: 40px;
        line-height: 12px;
        span {
          font-size: 12px;
        }
      }
    }
    .tips {
        font-size: 15px;
        margin-top: 40px;
      }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    .swiper-container {
      width: 630px;
      margin: 40px auto 0;
      .swiper-slide {
        height: 200px;
      }
      .script {
        height: 40px;
        line-height: 14px;
        span {
          font-size: 14px;
        }
      }
    }
    .tips {
        font-size: 16px;
        margin-top: 50px;
      }
  }
  @media screen and (min-width: 901px) and (max-width: 1200px) {
    .swiper-container {
      width: 720px;
      margin: 50px auto 0;
      .swiper-slide {
        height: 220px;
      }
      .script {
        height: 45px;
        line-height: 20px;
        span {
          font-size: 14px;
        }
      }
    }
    .tips {
        font-size: 16px;
        margin-top: 50px;
      }
  }
</style>


