<template>
  <div class="section">
    <transition name="photo-move">
      <div class="photo" v-show="show">
        <img src="../../static/img/photo.png" width="150" height="150" :class="{animated:show,jello:show}">
      </div>
    </transition>
    <transition name="photo-move">
      <div class="slogan" v-show="show">
        <span class="text"><span class="text-left">┌</span>最简单的事是坚持，最难的事也是坚持<span class="text-right">┘</span></span>
      </div>
    </transition>
    <div class="introduce">
      <transition name="photo-move">
        <p v-show="show">我叫江善威</p>
      </transition>
      <transition name="photo-move">
        <p v-show="show">一个认真肯学的小伙</p>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['show']
  }
</script>

<style lang="scss" scope>
  .section {
    overflow: hidden;
  }
  %public {
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .photo {
    @extend %public;
    transition: 1s;
    img {
      border: 5px solid rgba(255,255,255,0.9);
      border-radius: 50%;
      margin-top: 200px;
    }
  }
  .slogan {
    @extend %public;
    margin-top: 50px;
    transition: 1s 200ms;
    .text {
      position: relative;
      font: {
        size: 30px;
        weight: 400;
      }
      .text-left {
        position: absolute;
        top: -20px;
        left: -50px;
      }
      .text-right {
        position: absolute;
        bottom: -20px;
        right: -50px;
      }
    }
  }
  .introduce {
    @extend %public;
    p {
      margin:40px 0;
      font-size: 23px;
      &:first-of-type {
        transition: 1s 800ms;
      }
      &:last-of-type {
        transition: 1s 900ms;
      }
    }
  }
  .photo-move-enter-active, .photo-move-leave-active {
    transform: translateX(0);
  }
  .photo-move-leave-active {
    transition: 1s !important;
  }
  .photo-move-enter, .photo-move-leave-to {
    transform: translateY(200px);
    opacity: 0;
  }
  @media screen and (max-width: 460px) {
    .photo {
      img {
        width: 100px;
        height: 100px;
        margin-top: 100px;
      }
    }
    .slogan {
      .text {
        font-size: 16px;
        .text-left {
          top: -10px;
          left: -20px;
        }
        .text-right {
          bottom: -10px;
          right: -20px;
        }
      }
    }
    .introduce {
      margin-top: 30px;
      p {
        font-size: 14px;
        margin: 15px 0;
      }
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    .photo {
      img {
        width: 120px;
        height: 120px;
        margin-top: 110px;
      }
    }
    .slogan {
      .text {
        font-size: 20px;
        .text-left {
          top: -12px;
          left: -30px;
        }
        .text-right {
          bottom: -12px;
          right: -30px;
        }
      }
    }
    .introduce {
      margin-top: 35px;
      p {
        font-size: 17px;
        margin: 17px 0;
      }
    }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    .photo {
      img {
        width: 140px;
        height: 140px;
        margin-top: 120px;
      }
    }
    .slogan {
      .text {
        font-size: 25px;
        .text-left {
          top: -12px;
          left: -40px;
        }
        .text-right {
          bottom: -12px;
          right: -40px;
        }
      }
    }
     .introduce {
      margin-top: 50px;
      p {
        font-size: 20px;
        margin: 22px 0;
      }
    }
  }
</style>


