<template>
  <div id="app">
    <header>
      <div id="menu">
        <transition name="resume-avatar-move">
          <div class="resume-avatar" v-show="resumeAvatarShow">
            <a href="#index"><img src="../static/img/resume-avatar.jpg" width="40" height="40" class="animated"
            @mouseover="avatarAnimateFn" @mouseout="avatarAnimateFn" :class="{jello: avatarAnimate}"></a>
            <span class="text">前端求职简历</span>
          </div>
        </transition>
        <transition name="nav-move">
          <ul v-show="navShow">
            <li v-for="(item, index) in menuList" :data-menuanchor="anchorList[index]" @mouseover="swingOver(index)"
            @mouseout="swingOut" class="animated" :class="{swing:navIndex===index}">
              <a :href="'#'+anchorList[index]">{{item}}</a>
            </li>
          </ul>
        </transition>
      </div>
    </header>
    <section-one :show="showOne"></section-one>
    <section-two :show="showTwo"></section-two>
    <section-three :show="showThree"></section-three>
    <section-four :show="showFour"></section-four>
    <section-five :show="showFive"></section-five>
  </div>
</template>

<script>
  import sectionOne from './components/section-one.vue'
  import sectionTwo from './components/section-two.vue'
  import sectionThree from './components/section-three.vue'
  import sectionFour from './components/section-four.vue'
  import sectionFive from './components/section-five.vue'

  import 'fullpage.js/dist/jquery.fullpage.css'
  import 'fullpage'
  import 'animate.css'
  export default {
    data () {
      return {
        menuList: ['首页', '关于我', '技能', '作品', '联系我'],
        anchorList: ['index', 'about', 'skill', 'demo', 'contact'],
        navIndex: -1,
        avatarAnimate: false,
        showOne: false,
        showTwo: false,
        showThree: false,
        showFour: false,
        showFive: false,
        resumeAvatarShow: false,
        navShow: false
      }
    },
    components: {
      sectionOne,
      sectionTwo,
      sectionThree,
      sectionFour,
      sectionFive
    },
    methods: {
      swingOver (index) {
        this.navIndex = index;
      },
      swingOut () {
        this.navIndex = -1;
      },
      avatarAnimateFn () {
        this.avatarAnimate = !this.avatarAnimate; 
      }
    },
    mounted () {
      let _this = this;
      this.navShow = this.resumeAvatarShow = true;
      $('#app').fullpage({
        sectionsColor: ['#85ADA5','#109085', '#4B5C8C', '#48809A', '#9C946D'],
        anchors: this.anchorList,
        menu: '#menu',
        navigation: true,
        navigationTooltips: this.menuList,
        verticalCentered: false,
        afterLoad (anchor, index) {
          switch (index) {
            case 1:
              _this.showOne = true;
              break;
            case 2:
              _this.showTwo = true;
              break;
            case 3:
              _this.showThree = true;
              break;
            case 4:
              _this.showFour = true;
              break;
            case 5:
              _this.showFive = true;
              break;
          }
        },
        onLeave (index) {
          switch (index) {
            case 1:
              _this.showOne = false;
              break;
            case 2:
              _this.showTwo = false;
               break;
            case 3:
              _this.showThree = false;
              break;
            case 4:
              _this.showFour = false;
              break;
            case 5:
              _this.showFive = false;
          }
        }
      });
    },
  }
</script>

<style lang="scss">
  body {
    font-family: Helvetica, Tahoma, Arial, "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";
    color: #fff;
  }
  #menu {
    position: fixed;
    top: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.1);
    ul {
      float: right;
      margin: 22px;
    }
    .nav-move-enter-active, .nav-move-leave-active {
      transition: 1.5s;
      transform: translateX(0);
    }
    .nav-move-enter, .nav-move-leave-to {
      transform: translateX(200px);
      opacity: 0;
    }
    li {
      float: left;
      margin-left: 20px;
      a {
        color: #fff;
        text-shadow: 2px 2px 4px #000;
      }
    }
    .resume-avatar {
      position: absolute;
      top: 50%;
      left: 15px;
      margin-top: -20px;
      img {
        border-radius: 50%;
        vertical-align: middle;
      }
      span {
        font-size: 14px;
        vertical-align: middle;
        margin-left: 10px;
      }
    }
    .resume-avatar-move-enter-active, .resume-avatar-move-leave-active {
      transition: 1s;
      transform: translateX(0);
    }
    .resume-avatar-move-enter, .resume-avatar-move-leave-to {
      transform: translateX(-200px);
      opacity: 0;
    }
  }
  @media screen and (max-width: 460px) {
    .right {
      display: none;
    }
    #menu {
      ul {
        margin: 10px;
      }
      li {
        margin-left: 10px;
        font-size: 14px;
      }
      .resume-avatar {
        margin-top: -12px;
        .text {
          display: none;
        }
        img {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    #menu {
      ul {
        margin: 12px;
      }
      li {
        margin-left: 10px;
        font-size: 15px;
      }
      .resume-avatar {
        margin-top: -12px;
        img {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
</style>


