<template>
  <div class="section">
    <heading :content="'关于我'" :show="show"></heading>
    <div class="about-me">
      <div class="info-list">
        <transition-group tag="ul" name="icon">
          <li class="info-item" v-for="(item, index) in iconList" :key="index" v-show="show">
            <img :src="item" :class="{'icon-scale': iconIndex===index}" @mouseover="iconScaleOver(index)" @mouseout="iconScaleOut">
            <br>
            <span>{{introList[index]}}</span>
          </li>
        </transition-group>
      </div>
      <div class="info-text">
        <transition name="text">
          <p class="text-content" v-show="show">
            前端是一门有容乃大的行业，我并不是计算机专业出生而是电子商务专业。毕业后在电商行业工作了将近两年，大学课程里学习过html+css，后了解到这个行业对此产生了兴趣。为了能进入这一行业，我购买了书籍以及网络视频学习前端知识。了解的越多，越觉得前端知识体系涉及之广，然而我并没有退却，还是希望能够加入前端的队伍，正确地提高自己的工作技术能力。
          </p>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
  import heading from './heading.vue'
  export default {
    props: ['show'],
    data () {
      return {
        iconList: [
          '../../static/img/age.png',
          '../../static/img/education.png',
          '../../static/img/address.png',
          '../../static/img/state.png'
        ],
        introList: ['年龄：24', '学历：大专', '户籍：福建', '状态：求职'],
        iconIndex: -1
      }
    },
    methods: {
      iconScaleOver (index) {
        this.iconIndex = index;
      },
      iconScaleOut () {
        this.iconIndex = -1;
        return false;
      }
    },
    components: {
      heading
    }
  } 
</script>

<style lang="scss" scope>
  .section {
    overflow: hidden;
  }
  .about-me {
    .info-list {
      ul {
        width: 50%;
        margin: 70px auto;
        display: flex;
        justify-content: space-around;
        .info-item {
          flex: 1;
          @for $i from 1 to 5 {
            &:nth-of-type(#{$i}) {
              transition: 1s #{$i / 2.5}s;
            }
          }
          text-align: center;
          img {
            width: 60px;
            height: 60px;
            transition: 500ms;
          }
          span {
            display: inline-block;
            margin-top: 20px;
          }
        }
      }
    }
  }
  .icon-enter-active, .icon-leave-active {
    transform: translateX(0);
  }
  .icon-leave-active {
    transition: 1s 0s !important;
  }
  .icon-enter, .icon-leave-to {
    opacity: 0;
    transform: translateX(-280px);
  }
  .icon-scale {
    transform: scale(1.3);
  }
  .info-text {
    width: 50%;
    margin: 0 auto;
    .text-content {
      &.text-enter-active, &.text-leave-active {
        transition: 1s 2s;
        transform: translateY(0);
      }
      &.text-leave-active {
         transition: 1s;
       }
      &.text-enter, &.text-leave-to {
        opacity: 0;
        transform: translateY(200px);
      }
      text-indent: 35px;
      line-height: 40px;
    }
  }
  @media screen and (max-width: 460px) {
    .about-me {
      .info-list {
        ul {
          width: 90%;
          margin: 30px auto;
          .info-item {
            img {
              width: 40px;
              height: 40px;
            }
            span {
              font-size: 13px;
              margin-top: 15px;
            }
          }
        }
      }
    }
    .info-text {
      width: 90%;
      .text-content {
        font-size: 14px;
        line-height: 23px;
      }
    }
    .icon-enter, .icon-leave-to {
      transform: translateX(-100px);
    }
  }
  @media screen and (min-width: 461px) and (max-width: 640px) {
    .about-me {
      .info-list {
        ul {
          width: 80%;
          margin: 33px auto;
          .info-item {
            img {
              width: 45px;
              height: 45px;
            }
            span {
              font-size: 16px;
              margin-top: 16px;
            }
          }
        }
      }
    }
    .info-text {
      width: 80%;
      .text-content {
        font-size: 15px;
        line-height: 26px;
      }
    }
    .icon-enter, .icon-leave-to {
      transform: translateX(-120px);
    }
  }
  @media screen and (min-width: 641px) and (max-width: 900px) {
    .about-me {
      .info-list {
        ul {
          width: 70%;
          margin: 33px auto;
          .info-item {
            img {
              width: 50px;
              height: 50px;
            }
            span {
              font-size: 18px;
              margin-top: 20px;
            }
          }
        }
      }
    }
    .info-text {
      width: 70%;
      .text-content {
        font-size: 16px;
        line-height: 35px;
      }
    }
    .icon-enter, .icon-leave-to {
      transform: translateX(-160px);
    }
  }
  @media screen and (min-width: 901px) and (max-width: 1200px) {
    .about-me {
      .info-list {
        ul {
          width: 60%;
        }
      }
    }
    .info-text {
      width: 60%;
    }
    .icon-enter, .icon-leave-to {
      transform: translateX(-180px);
    }
  }
  @media screen and (min-width: 1201px) and (max-width: 1500px) {
    .icon-enter, .icon-leave-to {
      transform: translateX(-210px);
    }
  }
</style>


